<template>
  <div class="container">
   <nav class="navbar navbar-expand-lg navbar-light ">
    <a class="navbar-brand" href="#">Madango</a>
    <button
      class="navbar-toggler"
      type="button"
      data-toggle="collapse"
      data-target="#navbarSupportedContent"
      aria-controls="navbarSupportedContent"
      aria-expanded="false"
      aria-label="Toggle navigation"
    >
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav mr-auto">
        <li class="nav-item">
          <router-link :to="{ name: 'home' }" class="nav-link"
            >Home</router-link
          >
        </li>
        <li class="nav-item">
          <router-link :to="{ name: 'foods' }" class="nav-link"
            >Foods</router-link
          >
        </li>
      </ul>
      <ul class="navbar-nav">
        <li class="nav-item">
          <router-link :to="{name : 'keranjang'}" class="nav-link"
            >Keranjang <i class="fas fa-shopping-basket mx-2"></i>
            <span class="badge badge-warning">{{keranjang}}</span></router-link
          >
        </li>
      </ul>
    </div>
  </nav>
  </div>
</template>
<script>

 



 
export default {
  name : 'navbar',

  data : function(){
    return {
      keranjang : 0
    }
  },
  mounted(){
  
    this.getKeranjang()
  },
  methods : {
    async getKeranjang(){
    
      let data = localStorage.getItem('pemesan')
      if(data){
        let response = await this.axios.get(`api/product/count/${data}`)
        this.keranjang = response.data.pesanan
      }
    }
  }
};
</script>

