  <navbar v-if="reRender" ></navbar>
  <div class="container">
  <router-view v-on:keranjang="keranjang"></router-view>
  </div>
  <footerComponent/>
</template>

<script>

import navbar from './components/Navbar.vue'
import footerComponent from './components/Footer.vue'
export default {
  name: 'App',
  components: {
    navbar,
    footerComponent
  },
  data :  function(){
    return{
      reRender : true,
      render : 1
    }
  },
  methods :{
    keranjang(){
      this.reRender = false
      this.$nextTick(()=>{
        this.reRender = true
      })
     
    }
  }
}
</script>

