<template>
  <div class="container">
    <div class="row mt-4">
      <div class="col-md-6">
        <img :src="product.gambar" alt="" class="img-fluid" />
      </div>
      <div class="col-md-6">
        <h2>
          <strong>{{ product.nama }}</strong>
          <template v-if="product.is_ready == 1">
            <span class="badge badge-success">Tersedia</span>
          </template>
          <template v-else>
            <span class="badge badge-danger">Habis</span>
          </template>
        </h2>
        <h2>Rp. {{ product.harga }}</h2>
        <div class="row">
          <div class="col">
            <form v-on:submit.prevent="putInCart">
              <div class="table-responsive">
                <table class="table">
                  <tr>
                    <td>
                      <label for="nama">Nama anda:</label>
                    </td>
                    <td>
                      <template v-if="getPemesan() == false">
                       
                      <input
                        v-model="form.nama"
                        type="text"
                        name=""
                        id="nama"
                        class="form-control"
                        
                        
                        
                      />
                      </template>
                      <template v-else>
                       
                      <input
                        v-model="form.nama"
                        type="text"
                        name=""
                        id="nama"
                        class="form-control"
                        
                        disabled
                        
                      />
                      </template>

                      <div v-if="errors.nama" class="alert alert-danger">
                        {{ errors.nama }}
                      </div>
                    </td>
                  </tr>
                   <tr>
                    <td>
                      <label for="nomerMeja">Nomer meja anda :</label>
                    </td>
                    <td>

                      <template v-if="getPemesan() == false"> 
                       
                      <input
                        v-model="form.nomerMeja"
                        type="number"
                        name=""
                        id="nomerMeja"
                        class="form-control"
                      />
                      </template>
                      <input v-else
                        v-model="form.nomerMeja"
                        type="number"
                        name=""
                        id="nomerMeja"
                        class="form-control"
                        disabled
                      />

                      <div v-if="errors.nomerMeja" class="alert alert-danger">
                        {{ errors.nomerMeja }}
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <label for="jumlah">Jumlah Pesanan :</label>
                    </td>
                    <td>
                      <input
                        v-model="form.jumlah"
                        type="number"
                        name=""
                        id="jumlah"
                        class="form-control"
                      />
                      <div v-if="errors.jumlah" class="alert alert-danger">
                        {{ errors.jumlah }}
                      </div>
                    </td>
                  </tr>
                 
                  <tr>
                    <td><label for="keterangan">Keterangan :</label></td>
                    <td>
                      <textarea
                        v-model="form.keterangan"
                        id="keteranan"
                        cols="30"
                        placeholder="*ex) pedas,tidak pakai nasi, etc"
                      ></textarea>
                    </td>
                  </tr>
                  <tr>
                    <td colspan="2">
                      <button  class="btn btn-warning btn-block" type="submit">
                        <strong
                          ><i class="fas fa-shopping-cart"></i> Masukan
                          Keranjang</strong
                        >
                      </button>
                    </td>
                  </tr>
                </table>
              </div>
            </form>
          </div>
        </div>
      </div>
      <hr />
    </div>
  </div>
</template>

<script>

 


export default {
  name : 'productDetail',

  data: function() {
    return {
      
      product: {},
      form: {
        jumlah: "",
        keterangan: "",
      },
      errors: {
        nomorMeja : '',
        nama : '',
        jumlah : ''
      },
    };
  },

  mounted() {
    this.getFood();
    this.getPemesan();
  },
  methods: {
    async getFood() {
      let response = await this.axios.get(
        `api/product/search/${this.$route.params.id}`
      );
      this.product = response.data.product;
      this.product.gambar = require(`../assets/images/${this.product.gambar}`);
    },
    async putInCart() {
      
      try {
        this.errors = {}
        this.form.productId = this.product.id;
        let response = await this.axios.post(
          "api/product/store",
          this.form
        );

        if (response.status == 200) {

          if(this.getPemesan() == false)
          {
            localStorage.setItem('pemesan', this.form.nama)
            localStorage.setItem('nomerMeja', this.form.nomerMeja)
          }         

          this.$wkToast(`${this.product.nama} berhasil masuk keranjang`, {
            className: "wk-success",
            horizontalPosition: "right",
            verticalPosition: "top",
            duration: 3000,
            transition: "transition",
          });
          this.form = {}
          this.getPemesan()
          this.$emit('keranjang')
         
        }
      } catch (e) {
        console.log(e.response.data.errors)
        if(e.response.data.errors.nama)
        this.errors.nama = e.response.data.errors.nama[0];
        if(e.response.data.errors.nomerMeja)
        this.errors.nomerMeja = e.response.data.errors.nomerMeja[0];
        if(e.response.data.errors.jumlah)
        this.errors.jumlah = e.response.data.errors.jumlah[0];
        
        
    }
    },
    getPemesan() {
      let dataPemesan = localStorage.getItem("pemesan");
      if (!dataPemesan) return false;
      else{
        this.form.nama = localStorage.getItem('pemesan')
        this.form.nomerMeja = localStorage.getItem('nomerMeja')
      }
    },
    
  },
};
</script>

<style scoped>
.container {
  min-height: 85vh;
}
</style>
