<template>
  <div class="container">
    <div class="row">
      <div class="col">
        <h2><strong>Keranjang</strong> :</h2>
        <div class="row">
          <div class="col">
            <div class="table-responsive">
              <table class="table" style="text-align:center">
                <tr>
                  <td>No</td>
                  <td>Food</td>
                  <td>Gambar</td>
                  <td>Keterangan</td>
                  <td>Harga</td>
                  <td>Jumlah Pesanan</td>
                  <th>Total Harga</th>
                  <th></th>
                </tr>
                <template v-for="userData in userDatas" :key="userData.id">
                  <tr>
                    <td></td>
                    <td>{{ userData.product }}</td>
                    <td>
                      <img
                        :src="require(`../assets/images/${userData.gambar}`)"
                        alt=""
                        width="100"
                      />
                    </td>
                    <td>{{userData.keterangan}}</td>
                    <td>Rp. {{ userData.hargaProduct }}</td>
                    <td>{{ userData.jumlahPesanan }}</td>
                    <td>
                      <strong>Rp. {{ userData.totalHargaProduct }}</strong>
                    </td>
                    <td class="text-danger">
                      <i
                        @click="destroyProduct(userData.id)"
                        class="fas fa-trash"
                        style="cursor : pointer"
                      ></i>
                    </td>
                  </tr>
                </template>
                <tr class="text-right">
                  <td colspan="6"><strong>Pemesan :</strong></td>
                  <td>
                    <strong>{{ pemesan }}</strong>
                  </td>
                </tr>
                <tr class="text-right">
                  <td colspan="6"><strong>Nomer Meja :</strong></td>
                  <td>
                    <strong>{{ nomerMeja }}</strong>
                  </td>
                </tr>
                <tr class="text-right">
                  <td colspan="6"><strong>Total Harga :</strong></td>
                  <td>
                    <strong>Rp. {{ total }}</strong>
                  </td>
                </tr>
                <tr class="text-right">
                  <td colspan="7">
                    <button
                      v-if="userDatas[0]"
                      @click="checkout"
                      class="btn btn-warning"
                    >
                      <strong>Pesan Sekarang</strong>
                    </button>
                  </td>
                </tr>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function() {
    return {
      userDatas: [],
      pemesan: "",
      nomerMeja: "",
      total: "",
    };
  },
  mounted() {
    this.getUserData();
  },
  methods: {
    async getUserData() {
      if (localStorage.getItem("pemesan")) {
        let pemesan = localStorage.getItem("pemesan");
        let response = await this.axios.get(
          `api/product/cart/${pemesan}`
        );
        if (response.status == 200) {
          this.pemesan = response.data.data[0].pemesan;
          this.nomerMeja = response.data.data[0].nomerMeja;
          this.total = response.data.data[0].total;

          this.userDatas = response.data.data;
        } else {
          console.log("error");
        }
      }
    },
    async destroyProduct(id) {
      let response = await this.axios.delete(
        `api/product/${id}`
      );
      if (response.status == 200) {
        this.getUserData();
        this.$wkToast(`${response.data.message}`, {
          className: "wk-success",
          horizontalPosition: "right",
          verticalPosition: "top",
          duration: 3000,
          transition: "transition",
        });
        this.$emit("keranjang");
      }
    },
    async checkout() {
        let response = await this.axios.patch(
            `api/product/${this.pemesan}`
      );
   
      if (response.status == 200) {
          this.$emit("keranjang");
        this.getUserData();
        this.$wkToast(`${response.data.message}`, {
            className: "wk-success",
          horizontalPosition: "right",
          verticalPosition: "top",
          duration: 5000,
          transition: "transition",
        });
            localStorage.removeItem("pemesan");
            localStorage.removeItem("nomerMeja");
        this.$router.push('/')
      }
    },
  },
};
</script>

