<template>
  <div class="row my-4">
    <div class="col-md-6">
      <h2>Choose your <strong>Foods :</strong></h2>
    </div>
    <div class="col-md-6">
      <div class="input-group mb-3">
        <div class="input-group-prepend">
          <span class="input-group-text bg-warning" id="basic-addon1"><i class="fas fa-search"></i></span>
        </div>
        <input v-on:keyup="getProductByKey"
          type="text"
          class="form-control"
          placeholder="Search your foods"
          aria-label="Search your foods"
          aria-describedby="basic-addon1"
        />
      </div>
    </div>
  </div>
  <div class="row">
    <template v-for="product in products" :key="product.id">     
    <div class="col-md-4">
       <div class="card ">
          <img class="card-img-top rounded " :src="require(`../assets/images/${product.gambar}`)" alt="Card image cap " style="height:250px" />
          <div class="card-body">
            <h5 class="card-title"><strong>{{product.nama}}</strong></h5>
            <h5 class="card-title">Rp.  {{product.harga}}</h5>
            <p class="card-text">
             
            </p>
            <router-link :to="{name : 'foodDetail', params : { id : product.id}}" class="btn btn-warning btn-block"><strong> <i class="fas fa-eye"></i> Pesan</strong></router-link>
          </div>
        </div>
    </div>
    </template>
  </div>
</template>

<script>
export default {
  data : function(){
    return{
      products : '',
    }
  },
  mounted(){
    this.getProducts()
  },
  methods : {
    async getProducts(){
      let response = await this.axios.get(`api/product/all`)
      this.products = response.data.products
    },
    async getProductByKey(e){  
      let response = await this.axios.post(`api/product/search/`,{"keyword" : e.target.value})
      e.target.value ? this.products = response.data.products : this.getProducts()
      
    }
  }
};
</script>

